.image--text {
	background-image: url(../../assets/purple-clouds.jpg);
	background-size: cover;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	height: fit-content;
	width: max-content;
	background-attachment: fixed;
}
.image--text--2 {
	background-image: url(../../assets/purple-clouds-16-9.png);
	background-size: cover;
	height: fit-content;
	width: max-content;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-attachment: fixed;
}

// Phone
@media screen and (max-width: 768px) {
	.image--text,
	.image--text--2 {
		background-attachment: unset !important;
	}
}
